<template>
  <b-form class="text-center" style="margin-top:100px;" @submit="onSubmit" @reset="onReset" >
            
            <div v-show="selectedImage == false" >
              
              <div
              class="profile profile1"
              @click="onClickImageUpload">
                <img :src="tempUrl" :style="tempStyle">
              </div>

              <input
              ref="profileFile"
              class="imageInput"
              type="file"
              hidden
              @change="onChangeImages"
              accept="image/*"
              >
              <a
              v-show="tempUrl != basicUrl"
              class="step1-a"
              @click="onReset"
              ></a>

            
            </div>

            <div v-show="selectedImage">
            
              <v-zoomer
              ref="zoomer"
              class="profile"
              :max-scale="10"
              :zooming-elastic="false"
              :zoomed.sync="zoomed"
              >
              <img :src="tempUrl" class="profile-img">
              </v-zoomer>

              <a
              class="step1-a"
              @click="onCancel"
              ></a>
              
              <a
              class="step1-a"
              @click="getStyle"
              ></a>
            
            </div>
            
            <span class="error">{{ error }}</span>
            <Button v-show="selectedImage == false" class="mt-5" style="margin-top:100px" text="" />

  </b-form>
</template>

<script>

import Button from '../../../shared-components/small/Button.vue'
import { checkForm } from '../../../config/validator'

export default {
    name: "Step1",
    components: {
        Button,
    },
    props: {
      profileFrm: Object
     },
    computed: {
      form: function() {
        return this.profileFrm
      }
    },
    data() {
      return {
        basicUrl: require('../../../assets/img/basicProfile.png'), // 
        
        selectedImage: false, //  -  
        zoomed: false, //  

        oldTempUrl: null, //   
        oldTempStyle: null, //   
        tempStyle : null, // 
        tempUrl : null, // 

        error : null // 
      }
    },
    created() {
      this.tempUrl = this.basicUrl
      this.oldTempUrl = this.basicUrl
    },
    methods: {
      
      //       (   )
      //         selectedImage = false 
      //    zoomer reset (    )
      getStyle() {
        this.tempStyle = "object-fit: contain; width: 100%; height: 100%; transform:" 
                        + document.querySelector(".zoomer").style.transform
        this.selectedImage = !this.selectedImage

        this.oldTempUrl = this.tempUrl
        this.oldTempStyle = this.tempStyle

        this.$refs.zoomer.reset()
      },
      //     
      //   change file value 
      onClickImageUpload() {
        this.$refs.profileFile.value = null
        this.$refs.profileFile.click()
      },
      //    selectedImage = true  ( )
      //     URL 
      onChangeImages(e) {
        const file = e.target.files[0]
        if(file != null && file != ''){
          this.selectedImage = !this.selectedImage
          this.tempUrl = URL.createObjectURL(file)
        }
      },
      //      
      //   change file value 
      onCancel() {
        this.selectedImage = !this.selectedImage
        this.tempUrl = this.oldTempUrl
        this.tempStyle = this.oldTempStyle
        this.$refs.profileFile.value = null
        this.$refs.zoomer.reset()
      },
      //  
      onSubmit(e) {
        e.preventDefault()
        // url    
        this.form.imageUrl = document.querySelector(".imageInput").value
        this.error = checkForm('step1',this.form)
        this.form.imageUrl = this.tempUrl
        this.form.imageStyle = this.tempStyle
        
        if(this.tempUrl == this.basicUrl || this.error == null) {
          this.$emit('step',2)
        }
      },
      //  
      //   change file value 
      onReset() {
        this.$refs.profileFile.value = null
        this.tempStyle = null
        this.tempUrl = this.basicUrl

        this.oldTempUrl = this.basicUrl
        this.oldTempStyle = null
        
        this.zoomed = false
        this.selectedImage = false
      }
    }
}
</script>
