const blank = /[\s]/g
const idChk = /^[A-Za-z0-9]{4,15}$/ // , 4~15
const pwdChk = /^.*(?=^.{8,30}$)(?=.*\d)(?=.*[a-zA-Z])(?=.*[!@#$%^&+=]).*$/  // ,,    8~30
const nameChk = /^[-A-Za-z]{2,15}$/
const mailChk = /^[a-z0-9_+.-]+@([a-z0-9-]+\.)+[a-z0-9]{2,4}$/
const telChk = /^\d{3}\d{3,4}\d{4}$/

const imgChk = /(.*?)\.(jpg|jpeg|png|gif|bmp)$/


const checkForm = (type,val) => {
    

    switch (type) {

        case 'login' :
            break

        case 'step0' :
            
            if(nameChk.test(val.name) == false)  
                return "    2 15  "

            if(idChk.test(val.userId) == false) 
                return " ,  4 15  "

            if(pwdChk.test(val.password) == false) 
                return "  ,  8 30  "
        
            if(telChk.test(val.phone) == false) 
                return "    "
        
            if(mailChk.test(val.email) == false) 
                return "   "
        
            if(blank.test(val.userId) == true || blank.test(val.password) == true ||
               blank.test(val.name) == true || blank.test(val.phone) == true || blank.test(val.email) == true )
                return " "
            
            break
        
        case 'step1' :

            if(val.imageUrl != null && val.imageUrl != '' && imgChk.test(val.imageUrl) == false)
                return "  "
            
            break

        case 'step2' :

            for(var i in val){
                if(val[i].keyId == null) return "  "
            }

            break

        case 'step3' :

            for(var j in val){
                console.log(val[j].imageUrl)
                if(val[j].imageUrl != null && val[j].imageUrl != '' &&imgChk.test(val[j].imageUrl) == false)
                return "  "
            }

            break
        
        default :

            return null

    }

}

export { checkForm }
