<template>
    <div style="width:320px; margin:0 auto;">

        <h1>logo</h1>

        <b-form class="mt-5" @submit="onSubmit" @reset="onReset" >

            <b-form-input
            id="input-pwd"
            class="mb-2"
            v-model="form.oldPwd"
            placeholder=" "
            type="password"
            required
            />

            <b-form-input
            id="input-newpwd"
            class="mb-2"
            v-model="form.newPwd"
            placeholder=""
            type="password"
            required
            />

            <b-form-input
            id="input-newpwdchk"
            class="mb-3"
            v-model="form.newPwdCfm"
            placeholder="  "
            type="password"
            required
            />

            <Button text=" " />

        </b-form>

        <BottomLine text="  " url="/Login" />

    </div>
</template>

<script>

import Button from '../../shared-components/small/Button.vue'
import BottomLine from '../../shared-components/small/BottomLine.vue'


export default {
    name: "NewPwd",
    components: {
        Button,
        BottomLine,
    },
    data() {
      return {
        form: {
          oldPwd: '',
          newPwd: '',
          newPwdCfm: '',
        },
      }
    },
    methods: {
      onSubmit(event) {
        event.preventDefault()
        alert(JSON.stringify(this.form))
        this.$router.push('/LoginMessage/  ');
      },
      onReset(event) {
        event.preventDefault()
        this.form.oldPwd = ''
        this.form.newPwd = ''
        this.form.newPwdCfm = ''
      }
    }
}
</script>

<style>
    @import '../../assets/css/Login.css';
</style>
